import React from "react";
import { motion } from "framer-motion";
import styled from "styled-components";
import profile from "../images/profile.png";

const Hero = styled(motion.div)`
  width: ${(props) => (props.click === true ? "65vw" : " 0%")};
  display: flex;
  height: 55vh;
  position: fixed;

  background: linear-gradient(
        to right,
        ${(props) => props.theme.body} 50%,
        ${(props) => props.theme.text} 50%
      )
      bottom,
    linear-gradient(
        to right,
        ${(props) => props.theme.body} 50%,
        ${(props) => props.theme.text} 50%
      )
      top;
  background-repeat: no-repeat;
  background-size: 100% 2px;
  border-left: 2px solid ${(props) => props.theme.body};
  border-right: 2px solid ${(props) => props.theme.text};

  left: 50%;
  top: 50%;
  z-index: 2;
  transform: translate(-50%, -50%);
  @media (max-width: 750px) {
    flex-direction: column;

    border-right-width: initial;
    border-left-width: initial;
    border-right-color: initial;
    border-left-color: initial;
    background-image: linear-gradient(rgb(252, 246, 244) 50%, rgb(0, 0, 0) 50%),
      linear-gradient(rgb(252, 246, 244) 50%, rgb(0, 0, 0) 50%);
    background-position-x: 0px, 100%;
    background-position-y: 0px, 0px;
    background-size: 2px 100%;
    background-repeat-x: no-repeat;
    background-repeat-y: no-repeat;
    background-attachment: initial;
    background-origin: initial;
    background-clip: initial;
    background-color: initial;
    border-style: solid none;
    border-image: initial;
    border-top: 2px solid rgb(252, 246, 244);
    border-bottom: 2px solid rgb(0, 0, 0);
    background-position: 0px 0px, 100% 0px;
    background-repeat: no-repeat;
  }
`;

const Text = styled.div`
  font-size: calc(1em + 1.5vw);
  color: ${(props) => props.theme.body};
  padding: 2rem;
  cursor: pointer;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  & > *:last-child {
    color: ${(props) => `rgba(${props.theme.bodyRgba},0.6)`};
    font-size: calc(0.5rem + 1.5vw);
    font-weight: 300;
  }
  @media (max-width: 750px) {
    font-size: calc(1em);
  }
`;

export const SubHero = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 55vh;
  color: ${(props) => props.theme.text};
  @media (max-width: 750px) {
    width: 100%;
  }
  .pic {
    right: 50%;
    bottom: 0;
    transform: translate(50%, 0%);
    width: 25vh;
    height: auto;
    position: absolute;

    @media (max-width: 750px) {
      width: 15vh;
      right: 0%;
    }
  }
  .mot {
    @media (max-width: 750px) {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }
  z-index: 2;
`;
const Intro = (props) => {
  return (
    <Hero
      click={props.click}
      initial={{ height: 0 }}
      animate={{ height: "55vh" }}
      transition={{ type: "spring", duration: 1, delay: 0.8 }}
    >
      <SubHero>
        <Text>
          <h1>Hi,</h1>
          <h2>I am Aarsh.</h2>
          <h3>
            I am a Software Development Engineer specializing in Full Stack
            Development
          </h3>
        </Text>
      </SubHero>

      <SubHero>
        {" "}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 1 }}
          className="mot"
        >
          <img alt="profile" className="pic" src={profile}></img>
        </motion.div>
      </SubHero>
    </Hero>
  );
};

export default Intro;
