import { motion } from "framer-motion";
import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import styled from "styled-components";
import { DarkTheme } from "../Themes";

const Navbar = styled.div`
  display: flex;
  width: 30vw;
  justify-content: space-between;
  position: fixed;
  top: 2rem;
  right: 2rem;
  z-index: 3;
  padding: 0.5rem;
  @media (max-width: 900px) {
    width: 40vw;
  }
  @media (max-width: 650px) {
    width: 70vw;
  }
`;

const NavbarComponent = (props) => {
  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);
  const updateDimensions = () => {
    setWidth(window.innerWidth);
    setHeight(window.innerHeight);
  };
  useEffect(() => {
    window.addEventListener("resize", updateDimensions);
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);

  const colorFill = props.theme === "dark" ? DarkTheme.text : DarkTheme.body;
  return (
    <Navbar>
      <NavLink to="/about" style={{ color: `${colorFill}` }}>
        <motion.h3 whileHover={{ scale: 1.1 }}>About</motion.h3>
      </NavLink>
      <NavLink to="/experience" style={{ color: `${colorFill}` }}>
        <motion.h3 whileHover={{ scale: 1.1 }}>Experience</motion.h3>
      </NavLink>
      <NavLink to="/skills" style={{ color: `${colorFill}` }}>
        <motion.h3 whileHover={{ scale: 1.1 }}>Skills</motion.h3>
      </NavLink>
      <NavLink to="/blog" style={{ color: `${colorFill}` }}>
        <motion.h3 whileHover={{ scale: 1.1 }}>Blog</motion.h3>
      </NavLink>
    </Navbar>
  );
};

export default NavbarComponent;
