import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import NavbarComponent from "../subComponents/Navbar";
import SocialIcons from "../subComponents/SocialIcons";
import { DarkTheme } from "../Themes";
import LogoComponent from "./../subComponents/Logo";
import { motion } from "framer-motion";
import knife from "../images/knife.png";

import Intro from "./Intro";
import { pageMotion } from "../util/motion";

const Container = styled(motion.div)`
  height: 100vh;
  width: 100vw;
  position: relative;
  overflow: hidden;
`;
export const Center = styled.div`
  position: fixed;
  right: ${(props) => (props.click == true ? "5rem" : "50%")};
  bottom: ${(props) => (props.click == true ? "5rem" : "50%")};

  display: flex;
  justify-content: center;
  align-items: center;
  transform: translate(50%, 50%);
  transition: right 1s ease, bottom 1s ease;
  .pic {
    width: ${(props) => (props.click == true ? "5vh" : "15vh")};
    height: auto;
    @media (max-width: 750px) {
      width: ${(props) => (props.click == true ? "5vh" : "10vh")};
    }
  }
`;

const Left = styled.div`
  width: ${(props) => (props.click == true ? "50vw" : "0vw")};

  background-color: black;
  height: 100vh;
  z-index: 1;
  transition: height 0.5s ease, width 0.5s ease 0.5s;
  @media (max-width: 750px) {
    width: 100vw;
    height: ${(props) => (props.click == true ? "50vh" : "0vh")};
  }
`;

const Main = () => {
  const [click, setclick] = useState(false);
  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);
  const updateDimensions = () => {
    setWidth(window.innerWidth);
    setHeight(window.innerHeight);
    console.log("update dimensions " + width);
  };
  useEffect(() => {
    window.addEventListener("resize", updateDimensions);
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);

  const handleClick = () => {
    setclick(!click);
    console.log(click);
  };
  return (
    <Container
      initial="initial"
      animate="animate"
      exit="exit"
      variants={pageMotion}
    >
      <LogoComponent
        theme={click == true ? "dark" : "light"}
        handleClick={() => {
          handleClick();
        }}
      />
      {width < 750 ? (
        <NavbarComponent theme={click == true ? "dark" : "light"} />
      ) : (
        <NavbarComponent theme="light" />
      )}
      {width > 750 ? (
        <SocialIcons theme={click == true ? "dark" : "light"} />
      ) : (
        <SocialIcons theme="light" />
      )}
      <Left click={click} />
      {click === true ? <Intro click={click} /> : null}

      <Center click={click}>
        <motion.button
          style={{ border: "none", background: "none", cursor: " pointer" }}
          onClick={handleClick}
          animate={{ rotate: 360 }}
          transition={{ ease: "linear", duration: 2, repeat: Infinity }}
        >
          <motion.img className="pic" src={knife} alt="knife"></motion.img>
        </motion.button>
      </Center>
    </Container>
  );
};

export default Main;
